import { Typography } from "@mui/material";
import Stack from "@mui/material/Stack";

export default function Intro() {
  return (
    <Stack width={"100%"} alignItems="center" spacing={2}>
      <Typography
        variant="h4"
        fontWeight="bold"
        display={"flex"}
        alignItems="center"
        gap={"0.6rem"}
      >
        Hi I'm
        <Typography
          fontSize={{ xs: "1.25rem", lg: "2.125rem" }}
          component="span"
          color="primary.main"
        >
          Muhammad Ahmed
        </Typography>
      </Typography>
      <Typography
        fontSize={"1.125rem"}
        variant="body2"
        color="text.secondary"
        textAlign="center"
        width={"75%"}
      >
        I create stellar web experiences with modern technologies. Specializing
        in{" "}
        <Typography
          component={"span"}
          fontSize={"1.125rem"}
          variant="body1"
          color="primary"
        >
          Full Stack Development
        </Typography>
        , I build interfaces that are both beautiful and functional
      </Typography>
    </Stack>
  );
}
